<template>
  <div id="app">
    <Loading v-show='$root.$data.viewLoading'></Loading>
    <Header v-show='$root.$data.viewHeader'></Header>
    <keep-alive :exclude="['Detail']">
    <router-view></router-view>
    </keep-alive>
   <Footer v-show='$root.$data.viewFooter' ></Footer>
  </div>
</template>

<script>
import Header from './components/Header'
import Home from './pages/Home'
import Footer from './components/Footer'
import Loading from './components/Loading'

export default {
  name: 'app',
  watch:{   //因为app组件永远不会被卸载，所以对他进行数据观测，当有组件被挂载卸载时写业务逻辑
    $route({path}){  //解构赋值，拿到每个组件中的path
      // console.log("app",path)
      // home/follow/column true true
      // user/detail false  false
      // login/reg  false  true

      if(/home|follow|column/.test(path)){
        this.$root.$data.viewHeader = true;
        this.$root.$data.viewFooter = true;
      }
      if(/user|detail/.test(path)){
        this.$root.$data.viewHeader = false;
        this.$root.$data.viewFooter = false;
      }
      if(/login|user|echarts|bdmap/.test(path)){
        this.$root.$data.viewHeader = false;
        this.$root.$data.viewFooter = true;
      }
    },
    immediate: true
  },
  components: {
    Header,Home,Footer,Loading
  } 
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
